/* mixins */


/* end mixins */
.offcanvas {
	background-color: #e9e9e9;
}
.carousel-caption {
	top: 50%;
	transform: translateY(-50%);
	bottom: initial;
	-webkit-transform-style: preserve-3d;
	-moz-transform-style: preserve-3d;
	transform-style: preserve-3d;
	z-index: 1;
}
.carousel-item {
	height: 491px;
	@include breakpoint (lg) {
		min-height: 491px;
		width: 100%;
	}
	&:after {
		content:"";
		display:block;
		position:absolute;
		top:0;
		bottom:0;
		left:0;
		right:0;
		background:rgba(0,0,0,0.7);	
	}
}
.carousel-item img {
    position: absolute;
    top: 0;
    left: 0;
    min-height: 491px;
    @include breakpoint (lg) {
    	min-height: 100%;
    	width: 100%;
    	top: 0;
    	left: 0;
    }

}
